import { ChatFileContext } from "@rp/react/common/hooks"
import { Box, Fade, Skeleton } from "@rp/react/web/mui/core"
import { useChatTitle } from "../../useChatFileTitle"
import { ChatSuggestions } from "./ChatSuggestions"

type Props = {
  filesIds: ChatFileContext["filesIds"]
  isChatEmpty: boolean
}
const ChatFileContextDisplay = ({ filesIds, isChatEmpty }: Props) => {
  const { title: fileTitle, isLoading } = useChatTitle({
    files: { fileIds: filesIds ?? [], multipleTitle: "" },
    watchlist: { watchlistId: undefined },
  })

  if (isLoading) {
    return (
      <Fade in>
        <div style={{ width: "100%" }}>
          <ChatSuggestions isChatEmpty={isChatEmpty}>
            <Skeleton width={"100%"} />
            <Skeleton width={"50%"} />
          </ChatSuggestions>
        </div>
      </Fade>
    )
  }

  return (
    <Fade in>
      <Box sx={{ "& .MuiTypography-root": { wordBreak: "break-word" } }}>
        <ChatSuggestions isChatEmpty={isChatEmpty}>
          {(filesIds?.length ?? 0) > 1
            ? "How can I assist you with the information in these documents?"
            : `How can I assist you with the information in the document “${fileTitle}”?`}
          <br />
          If you want a <strong>summary</strong> or have <strong>specific questions</strong>, please
          let me know!
        </ChatSuggestions>
      </Box>
    </Fade>
  )
}

export { ChatFileContextDisplay }
